@import url('https://fonts.googleapis.com/css?family=Open+Sans|Lato');

@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,700&display=swap');

html, body {
  margin: 0;
  padding: 0;

  background-color: black;
  color: rgb(204, 204, 204);
}

h1{
  color: white;
}

h1, h2, h3 {
  font-family: "Segoe UI", sans-serif;
}

section h2 {
  font-size: 2em;
  font-weight: 400;
  color: rgb(214, 214, 214);
}

p {
  font-family: "Open Sans", sans-serif;

}

body {
  max-width: 100%;
}

@media screen and (max-width: 40em) {
  .features h2, .features p {
    text-align: center;
  }
  
  .features_list li h3 {
    color: white;
  }

  .landing_text h1 {
    font-size: 2.4em;
  }
}

@media screen and (min-width: 50em) {
  
  .features_list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    font-size: 1em;
  }
  
  .content {
    padding-top: 15vh;
  }

  .features_list li {
    display: flex;
    flex-direction: column;
    flex: 1 0 33%;
  }

  .features_list li p {
    width: 70%;
  }

  nav ul {
    margin-left: 20vh;
  }

  .rhs_container {
    margin-left: 15vh;
  }

  .landing_text h1 {
    color: white;
    font-size: 3em;
  }

  .sticky {
    position: fixed;
  }
}


@media screen and (min-width: 35em) {  
  .comparison {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .comparison article {
    width: 50%;
  }

  .landing_hero {
    width: 100%;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: auto;
    grid-template-areas: "lhero_left lhero_right";

    font-family: "Roboto", sans-serif;

  }

  .landing_text {
    grid-area: lhero_left;
    width: 60%;
    margin-top: 20vh;
  }

  .container {
    width: 100%;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: auto;
    grid-template-areas: "topleft topright";
    color: rgb(192, 192, 192);
    margin-left: 0;
    padding-left: 0;
  }

  .landing_image {
    margin-top: 10vh;
    grid-area: lhero_right;
  }
  
  .guide_pane .container {
    grid-area: "guide_rhs";
  }

  p {
    max-width: 100%;
  }
  
  .logo {
    grid-area: topleft;
    align-items: flex-start;
    color: rgb(192, 192, 192);
    font-size: 1.2em;
    padding: 0.7em;
  }

  .contright {
    grid-area: topright;
  }


  .guide_pane, .feature_pane {
    width: 100%;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: auto;
    grid-template-areas: "guide_lhs guide_rhs";

  }


  .guide_pane section {
    grid-area: "guide_lhs";
    width: 70%;
  }


  footer {
    width: 100%;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: auto;
    grid-template-areas: "footer_text footer_links";
    margin-top: 10vh;
  }


  .footer_lhs {
    grid-area: "footer_text";
  }

  .footer_rhs {
    grid-area: "footer_links";
  }

  .content {
    margin-left: 15vh;
  }

}

